.App-link {
  color: #61dafb;
}


.header { 
  background: -webkit-linear-gradient(235deg,rgba(255, 196, 0, 0.9),rgb(255, 196, 0, 0) 70%), -webkit-linear-gradient(320deg, rgba(255, 0, 132, 0.8), rgb(255,0,0,0) 70%), -webkit-linear-gradient(80deg, rgba(183, 0, 255, 0.9), rgb(255,0,0,1) 100%); 
  background: linear-gradient(215deg,rgba(255, 196, 0, 0.9),rgb(255, 196, 0, 0) 70%), linear-gradient(130deg, rgba(255, 0, 132, 0.8), rgb(255,0,0,0) 70%), linear-gradient(10deg, rgba(183, 0, 255, 0.9), rgb(255,0,0,1) 100%);
  border-radius: 0% 60%;
  height: 100px;
  display: -webkit-flex;
  display: flex;
  text-align: center;
  color: white;
  font-size: calc(10px + 2vmin);
}

.header__store-title { 
  font-family: "Mr Dafoe";
  font-size: 50px;
  font-weight: 100;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  margin-top: 15px;
  margin-left: 65px;;
}

.header__cart { 
  -webkit-flex: 0 1 100px; 
          flex: 0 1 100px;
}


.header__cart-link{ 
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: none;
  font-family: "Muli";
  font-size: 20px;
  font-weight: bold;
  color: rgba(255, 0, 132);
  padding-right: 15px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}

.header__cart-link:hover { 
 color: orange;
}







.collection-viewer { 
  width: 80%;
  margin: auto;
}
.product{
  -webkit-flex: 1 0 30vh;
          flex: 1 0 30vh;
  margin: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: space-between;
          justify-content: space-between
}
.product__image { 
  width: 100%;
}

.collection__product-container{ 
  display: -webkit-flex; 
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
